.contentAbout {
    display: flex;
    flex-direction: column;
    align-items:center;
    margin: 0 15vw 0 15vw;
}


    .contentAbout .first-block {
        width: 90vw;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        background-color: #007FFF;
        text-align: justify;
    }

    .contentAbout .portrait-container {
        flex: 1;
        display:flex;
        justify-content:center;
        align-items:center;
        box-sizing: border-box;
        width: 40%;
        min-width: 350px;
        background: white;
    }
    .contentAbout .portrait {
        height: 43vh;
    }

    .contentAbout .hello-container {
        flex: 1;
        padding: 0 6vw 0 6vw;
    }



    .contentAbout .standard-block {
        background-color: indianred;
        display: flex;
        width: 90vw;
        flex-direction: column;
        align-items: center;
    }

LogoNameBlock {
    display: flex;
    flex-direction: column;
}
.download-button {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    text-align: center;
    font-family: "Coolvetica";
    color: rgba(13, 6, 0, 1);
    background-color: white;
    box-shadow: 0rem 0.6rem 0.6rem -1rem rgba(255,255,255,0.3);
    margin: 0.8vw;
    padding: 0.8vw;
}

    .download-button:hover {
        color: white;
        background-color: rgba(0, 0, 0, 1);
        cursor: pointer;
    }
