.contentHome{
    display:flex;
    justify-content:center;
}

.contentHome .title {
    font-family: "Cocogoose";
    color: white;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.contentHome .title span:nth-child(2) {
    animation-delay: 200ms;
}

.contentHome .title-hello {
    font-size: 10.7vw;
    padding-top: 1vh;
    line-height: 85%;
}
.contentHome .title-welcome {
    font-size: 4vw;
    padding-top: 1vh;
    line-height: 100%;
}

.contentHome .title-hello, .title-welcome {
    display: block;
    animation: textIn 500ms forwards;
}

@keyframes textIn{
    from {
        transform: translateY(100%);
    }
    to {
        transform: translateY(0%);
    }
}